.job-item {
  padding: 10px;
  text-decoration: none;
  color: #74718f;
  background-color: white;
  border: 1px solid #e6e7e9;
  border-radius: 0.3em;
  margin-bottom: 1em;
  position: relative;
  z-index: 1;
  display: table;
  width: 100%;
  transition:  all 0.3s ease-in-out;
}

.job-item:hover {
  cursor: pointer;
  box-shadow: 0px 2px 40px 0 rgba(0, 0, 0, 0.25)
}

.job-item_title {
  font-weight: 900;
  color: black;
}

.job-item_info {
  font-weight: 400;
  color: black;
}

.job-list {
  padding-top: 20px;
  padding-left: 100px;
  padding-right: 100px;
}